import axios from 'axios';
import { useEffect, useState } from 'react';
import bytes2Size from '../bytes2Size';

const useFetchBooks = (query: string, page: number) => {
  const [books, setBooks] = useState<any>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [isLoaded, setIsLoaded] = useState(false);

  useEffect(() => {
    if (!query) return;
    const fetchBooks = async () => {
      await axios
        .get(
          `https://kitapkurdu.onrender.com/books/search?name=${query}&page=${page}`
        )
        .then((res: any) => res.data)
        .then((d: any) => {
          return Promise.all(
            d.results.map((e: any) => {
              const path = e.path;
              const url = e.url;
              const { _id: id } = e;
              if (url) {
                return {
                  name: e.name,
                  file: e.url,
                  date: new Date(e.date).toLocaleDateString(),
                  size: e.size,
                  id,
                };
              } else {
                return fetch(
                  encodeURI(
                    `https://cloud-api.yandex.net/v1/disk/public/resources/download?public_key=https://disk.yandex.com.tr/d/sLURXWsHH4gDmw&path=${path}`
                  )
                )
                  .then((res) => res.json())
                  .then((res) => {
                    return {
                      name: e.name,
                      size: bytes2Size(e.size),
                      date: new Date(e.date).toLocaleDateString(),
                      file: res.href,
                      id: e.id || e._id,
                    };
                  });
              }
            })
          )
            .then((data: any) => {
              setBooks({
                results: data,
                total: d.total,
                next: d.next,
                previous: d.previous,
              });
              setIsLoaded(true);
              setIsLoading(false);
            })
            .catch((err) => {
              console.log(err);
            })
            .finally(() => {
              setIsLoading(false);
            });
        });
    };
    fetchBooks();
  }, [query, page]);

  return { books, isLoading, isLoaded };
};

export default useFetchBooks;
